* {
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}
p,
ul,
input {
  margin: 0;
}
ul {
  list-style: none;
  padding-left: 0;
}
.container {
  margin-left: auto;
  margin-right: auto;
  max-width: 50vw;
}

.country__title {
  font-size: 1.5em;
  margin-left: 1.5em;
}

.wrapper {
  margin-bottom: 2em;
}

#search-box {
  padding: 10px;
  width: 500px;
  font: inherit;
  font-size: 1.5em;
}

.country-list__item {
  display: flex;
  align-items: center;
  font-size: 1.5em;
  font-weight: 700;
}
.country-list__item:not(:last-child) {
  margin-bottom: 1em;
}
.country-list__img {
  margin-right: 10px;
}

.country-info {
  margin-top: 1em;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 1em;
  font-size: 1.5em;
}
.country-info__subtitle {
  font-weight: 700;
}
.country-info__text {
  font-weight: 500;
}

.country-languages {
  font-weight: 500;
}
.country-languages__item:not(:last-child) {
  margin-bottom: 0.5em;
}